	module.exports = "<template>\n<div>\n  <mu-raised-button label=\"show snackbar\" class=\"demo-snackbar-button\" @click=\"showSnackbar\"/>\n  <mu-raised-button label=\"show toast\" class=\"demo-snackbar-button\" @click=\"showToast\"/>\n  <mu-snackbar v-if=\"snackbar\" message=\"一段简单的文本\" action=\"关闭\" @actionClick=\"hideSnackbar\" @close=\"hideSnackbar\"/>\n  <mu-toast v-if=\"toast\" message=\"一段简单的文本\" action=\"关闭\" @close=\"hideToast\"/>\n</div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      snackbar: false,\n      toast: false\n    }\n  },\n  methods: {\n    showSnackbar () {\n      this.snackbar = true\n      if (this.snackTimer) clearTimeout(this.snackTimer)\n      this.snackTimer = setTimeout(() => { this.snackbar = false }, 2000)\n    },\n    hideSnackbar () {\n      this.snackbar = false\n      if (this.snackTimer) clearTimeout(this.snackTimer)\n    },\n    showToast () {\n      this.toast = true\n      if (this.toastTimer) clearTimeout(this.toastTimer)\n      this.toastTimer = setTimeout(() => { this.toast = false }, 2000)\n    },\n    hideToast () {\n      this.toast = false\n      if (this.toastTimer) clearTimeout(this.toastTimer)\n    }\n  }\n}\n</script>\n\n<style lang=\"css\">\n.demo-snackbar-button{\n  margin: 12px;\n}\n</style>\n"

